import React, { useState, useMemo, useRef, useEffect, useCallback } from 'react';
import { Word } from '../../types';
import { IconSearch, IconCheck } from '../Icons';
import { useDebounce } from '../../hooks';

interface WordListProps {
  words: Word[];
  learnedIds: Set<string>;
  onMarkAsLearned: (id: string) => void;
  title: string;
  showMarkButton?: boolean;
}

const ITEMS_PER_PAGE = 50;

export const WordList: React.FC<WordListProps> = ({
  words,
  learnedIds,
  onMarkAsLearned,
  title,
  showMarkButton = true,
}) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [displayCount, setDisplayCount] = useState(ITEMS_PER_PAGE);
  const [isLoading, setIsLoading] = useState(false);
  const listContainerRef = useRef<HTMLDivElement>(null);
  const debouncedSearchTerm = useDebounce(searchTerm, 300);

  const filteredWords = useMemo(() => {
    if (!debouncedSearchTerm) return words;
    
    const lowerTerm = debouncedSearchTerm.toLowerCase();
    return words.filter(w => 
      w.english.toLowerCase().includes(lowerTerm) || 
      w.chinese.includes(lowerTerm)
    );
  }, [words, debouncedSearchTerm]);

  const visibleWords = useMemo(() => {
    return filteredWords.slice(0, displayCount);
  }, [filteredWords, displayCount]);

  const hasMore = displayCount < filteredWords.length;

  // åŠ è½½æ›´å¤šå•è¯
  const loadMore = useCallback(() => {
    if (isLoading || !hasMore) return;
    
    setIsLoading(true);
    setTimeout(() => {
      setDisplayCount(prev => {
        const next = Math.min(prev + ITEMS_PER_PAGE, filteredWords.length);
        return next;
      });
      setIsLoading(false);
    }, 100);
  }, [isLoading, hasMore, filteredWords.length]);

  // ä¼˜åŒ–çš„æ»šåŠ¨å¤„ç†å‡½æ•°
  const handleScroll = useCallback((e: React.UIEvent<HTMLDivElement>) => {
    const { scrollTop, scrollHeight, clientHeight } = e.currentTarget;
    const scrollBottom = scrollTop + clientHeight;
    // å½“æ»šåŠ¨åˆ°è·ç¦»åº•éƒ¨ 100px æ—¶è§¦å‘åŠ è½½
    const threshold = scrollHeight - 100;
    
    if (scrollBottom >= threshold) {
      loadMore();
    }
  }, [loadMore]);

  // æœç´¢è¯å˜åŒ–æ—¶é‡ç½®
  useEffect(() => {
    setDisplayCount(ITEMS_PER_PAGE);
    if (listContainerRef.current) {
      listContainerRef.current.scrollTop = 0;
    }
  }, [debouncedSearchTerm]);

  // åˆå§‹åŠ è½½æ—¶ï¼Œå¦‚æœå†…å®¹ä¸å¤Ÿå¡«å……å®¹å™¨ï¼Œè‡ªåŠ¨åŠ è½½æ›´å¤š
  useEffect(() => {
    const container = listContainerRef.current;
    if (!container) return;

    // æ£€æŸ¥æ˜¯å¦éœ€è¦åŠ è½½æ›´å¤š
    const checkAndLoadMore = () => {
      const { scrollHeight, clientHeight } = container;
      // å¦‚æœå†…å®¹é«˜åº¦å°äºå®¹å™¨é«˜åº¦ï¼Œä¸”è¿˜æœ‰æ›´å¤šæ•°æ®ï¼Œåˆ™åŠ è½½æ›´å¤š
      if (scrollHeight <= clientHeight && hasMore && !isLoading) {
        loadMore();
      }
    };

    // å»¶è¿Ÿæ£€æŸ¥ï¼Œç¡®ä¿ DOM å·²æ›´æ–°
    const timer = setTimeout(checkAndLoadMore, 100);
    return () => clearTimeout(timer);
  }, [visibleWords.length, hasMore, isLoading, loadMore]);

  return (
    <div className="bg-white rounded-xl sm:rounded-2xl shadow-sm border border-slate-200 overflow-hidden flex flex-col h-full animate-fade-in">
      {/* Header */}
      <div className="p-4 border-b border-slate-100 bg-white sticky top-0 z-20 space-y-3">
        <div className="flex items-center justify-between">
          <h2 className="font-bold text-lg text-slate-800">{title}</h2>
          <span className="text-xs font-medium bg-slate-100 px-2 py-1 rounded text-slate-500">
            {filteredWords.length} words
          </span>
        </div>
        <div className="relative">
          <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none text-slate-400">
            <IconSearch />
          </div>
          <input 
            type="text" 
            placeholder="Search words..." 
            className="w-full pl-10 pr-4 py-2.5 rounded-lg bg-slate-50 border border-slate-200 focus:bg-white focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 outline-none transition-all duration-200 text-sm"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
      </div>

      {/* Word List */}
      <div 
        ref={listContainerRef}
        onScroll={handleScroll}
        className="flex-1 overflow-y-auto min-h-0"
      >
        <div className="divide-y divide-slate-50">
          {visibleWords.map((word) => {
            const isLearned = learnedIds.has(word.id);
            
            return (
              <div 
                key={word.id}
                className="p-4 hover:bg-slate-50 flex items-center justify-between group transition-colors duration-200"
              >
                <div className="flex-1 pr-4 min-w-0">
                  <div className="flex items-baseline gap-2">
                    <span className="font-bold text-slate-800 text-lg truncate">{word.english}</span>
                    <span className="font-mono text-slate-400 text-xs sm:text-sm shrink-0">{word.phonetic}</span>
                  </div>
                  <div className="text-sm text-slate-600 mt-1 line-clamp-2 leading-relaxed">{word.chinese}</div>
                </div>
                {isLearned ? (
                  <div className="shrink-0 flex flex-col items-center gap-1">
                    <span className="w-8 h-8 bg-emerald-100 text-emerald-600 rounded-full flex items-center justify-center">
                      <IconCheck />
                    </span>
                    <span className="text-[10px] font-bold text-emerald-600 uppercase tracking-wide">Mastered</span>
                  </div>
                ) : (
                  showMarkButton && (
                    <button 
                      onClick={() => onMarkAsLearned(word.id)}
                      className="shrink-0 w-8 h-8 text-slate-300 hover:text-indigo-500 hover:bg-indigo-50 rounded-full flex items-center justify-center transition-all duration-200"
                      title="Mark as learned"
                    >
                      <IconCheck />
                    </button>
                  )
                )}
              </div>
            );
          })}
        </div>
        
        {/* åŠ è½½æŒ‡ç¤ºå™¨ */}
        {hasMore && (
          <div className="p-4 text-center">
            {isLoading ? (
              <>
                <div className="inline-block w-6 h-6 border-2 border-slate-200 border-t-indigo-500 rounded-full animate-spin"></div>
                <p className="text-xs text-slate-400 mt-2">Loading more...</p>
              </>
            ) : (
              <button
                onClick={loadMore}
                className="px-4 py-2 text-sm text-indigo-600 hover:text-indigo-700 hover:bg-indigo-50 rounded-lg transition-colors"
              >
                Load More ({filteredWords.length - displayCount} remaining)
              </button>
            )}
          </div>
        )}
        
        {/* å…¨éƒ¨åŠ è½½å®Œæˆæç¤º */}
        {!hasMore && filteredWords.length > 0 && (
          <div className="p-4 text-center">
            <p className="text-xs text-slate-400">
              All {filteredWords.length} words loaded
            </p>
          </div>
        )}
        
        {filteredWords.length === 0 && (
          <div className="p-10 text-center text-slate-400 flex flex-col items-center gap-2">
            <IconSearch />
            <p>No words found matching &quot;{debouncedSearchTerm}&quot;</p>
          </div>
        )}
      </div>
    </div>
  );
};
