import { useState, useEffect, useRef } from 'react';

/**
 * è‡ªå®šä¹‰ Hookï¼šå®ç°é˜²æŠ–åŠŸèƒ½
 * @param value - éœ€è¦é˜²æŠ–çš„å€¼
 * @param delay - å»¶è¿Ÿæ—¶é—´ï¼ˆæ¯«ç§’ï¼‰
 * @returns é˜²æŠ–åçš„å€¼
 */
export function useDebounce<T>(value: T, delay: number): T {
  const [debouncedValue, setDebouncedValue] = useState<T>(value);

  useEffect(() => {
    // è®¾ç½®å®šæ—¶å™¨
    const timer = setTimeout(() => {
      setDebouncedValue(value);
    }, delay);

    // æ¸…ç†å‡½æ•°ï¼šåœ¨å€¼å˜åŒ–æ—¶æ¸…é™¤ä¹‹å‰çš„å®šæ—¶å™¨
    return () => {
      clearTimeout(timer);
    };
  }, [value, delay]);

  return debouncedValue;
}

/**
 * è‡ªå®šä¹‰ Hookï¼šå®ç°é˜²æŠ–å›è°ƒå‡½æ•°
 * @param callback - éœ€è¦é˜²æŠ–çš„å›è°ƒå‡½æ•°
 * @param delay - å»¶è¿Ÿæ—¶é—´ï¼ˆæ¯«ç§’ï¼‰
 * @returns é˜²æŠ–åçš„å›è°ƒå‡½æ•°
 */
export function useDebouncedCallback<T extends (...args: unknown[]) => unknown>(
  callback: T,
  delay: number
): (...args: Parameters<T>) => void {
  const timeoutRef = useRef<ReturnType<typeof setTimeout> | null>(null);

  return (...args: Parameters<T>) => {
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }

    timeoutRef.current = setTimeout(() => {
      callback(...args);
    }, delay);
  };
}
