import { useState, useEffect, useCallback } from 'react';

/**
 * è‡ªå®šä¹‰ Hookï¼šä½¿ç”¨ localStorage å®ç°çŠ¶æ€æŒä¹…åŒ–
 * @param key - localStorage é”®å
 * @param initialValue - åˆå§‹å€¼
 * @returns [storedValue, setValue] - å­˜å‚¨çš„å€¼å’Œæ›´æ–°å‡½æ•°
 */
export function useLocalStorage<T>(key: string, initialValue: T): [T, (value: T | ((val: T) => T)) => void] {
  // è·å–åˆå§‹å€¼
  const readValue = useCallback((): T => {
    if (typeof window === 'undefined') {
      return initialValue;
    }

    try {
      const item = window.localStorage.getItem(key);
      if (!item) return initialValue;

      const parsed = JSON.parse(item);
      
      // ç‰¹æ®Šå¤„ç†ï¼šå°†æ•°ç»„è½¬æ¢ä¸º Setï¼ˆç”¨äº learnedIdsï¼‰
      if (initialValue instanceof Set && Array.isArray(parsed)) {
        return new Set(parsed) as T;
      }
      
      return parsed as T;
    } catch {
      // é™é»˜å¤„ç†é”™è¯¯ï¼Œé¿å…æ³„éœ²å†…éƒ¨ä¿¡æ¯åˆ°æ§åˆ¶å°
      return initialValue;
    }
  }, [initialValue, key]);

  const [storedValue, setStoredValue] = useState<T>(readValue);

  // è¿”å›ä¸€ä¸ªåŒ…è£…åçš„ setState å‡½æ•°ï¼ŒåŒæ—¶æ›´æ–° localStorage
  const setValue = useCallback((value: T | ((val: T) => T)) => {
    try {
      // å…è®¸ value æ˜¯ä¸€ä¸ªå‡½æ•°
      const valueToStore = value instanceof Function ? value(storedValue) : value;
      
      // ä¿å­˜åˆ° state
      setStoredValue(valueToStore);
      
      // ä¿å­˜åˆ° localStorageï¼ˆSet è½¬æ¢ä¸ºæ•°ç»„ï¼‰
      if (typeof window !== 'undefined') {
        const valueToSave = valueToStore instanceof Set 
          ? Array.from(valueToStore)
          : valueToStore;
        window.localStorage.setItem(key, JSON.stringify(valueToSave));
      }
    } catch {
      // é™é»˜å¤„ç†é”™è¯¯ï¼Œé¿å…æ³„éœ²å†…éƒ¨ä¿¡æ¯åˆ°æ§åˆ¶å°
    }
  }, [key, storedValue]);

  // ç›‘å¬å…¶ä»–æ ‡ç­¾é¡µçš„å­˜å‚¨å˜åŒ–
  useEffect(() => {
    const handleStorageChange = (event: StorageEvent) => {
      if (event.key === key && event.newValue) {
        try {
          const parsed = JSON.parse(event.newValue);
          if (initialValue instanceof Set && Array.isArray(parsed)) {
            setStoredValue(new Set(parsed) as T);
          } else {
            setStoredValue(parsed);
          }
        } catch {
          // é™é»˜å¤„ç†è§£æé”™è¯¯
        }
      }
    };

    window.addEventListener('storage', handleStorageChange);
    return () => window.removeEventListener('storage', handleStorageChange);
  }, [key, initialValue]);

  return [storedValue, setValue];
}
